import React, { FC, useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Col, CustomInput, Row } from 'reactstrap';
import { InputGroupInline, InputType, WindowComponent, WindowComponentProps } from 'summer';
import { hasEmpresa } from '../../../../../dependencies/empresaDependency';
import { hasEmpresaMFP } from '../../../../../dependencies/empresaMFPDependency';
import { hasGrupoEmpresaConfig } from '../../../../../dependencies/grupoEmpresaConfigDependency';
import { CLASS_NAME as CLASS_NAME_EMPRESA } from '../../../../../models/dtos/ger/empresa';
import { getDefaultReportBasicValues } from '../../../../../models/dtos/ger/reportBasicValues';
import { getDefaultReportViewOptions, ReportViewOptions } from '../../../../../models/dtos/ger/reportViewOptions';
import { Reducers } from '../../../../../store/ducks';
import { GlobalState } from '../../../../../store/ducks/global.duck';
import EmpresaIS from '../../../../components/ger/empresaIS/EmpresaIS';
import TomadorServicoIS from '../../../../components/mfp/tomadorServicoIS/TomadorServicoIS';
import VinculoIS from '../../../../components/mfp/vinculoIS/VinculoIS';
import ReportForm from '../../../../components/report/ReportForm';
import { CLASS_KEY, REPORT_EVENT, REPORT_TITLE, RpaRV } from './rpaRV';

const DEFAULT_VALUES: RpaRV = {
  ...getDefaultReportBasicValues(REPORT_TITLE),
};

const DEFAULT_OPTIONS: ReportViewOptions = {
  ...getDefaultReportViewOptions(REPORT_EVENT),
  exibirConfiguracaoDadosEmpresa: false,
};

const RpaReportView: FC<WindowComponentProps & RouteComponentProps> = props => {
  const { handleSubmit, ...formProps } = useForm<RpaRV>({ defaultValues: DEFAULT_VALUES, mode: 'onBlur' });
  const { globalParameter } = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const { gerarPorEmpresas } = formProps.getValues();

  useEffect(
    () => {
      setTimeout(() => {
        formProps.setValue('referencia', globalParameter.empresaMFP.referencia);
      }, 200);
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const onChangeGerarPorEmpresas = () => {
    formProps.setValue('empresasSelecionadas', []);
    formProps.setValue('autonomos', []);
    formProps.setValue('tomadores', []);
  };

  const validateExtras = () => {
    const erros = [];

    if (gerarPorEmpresas && formProps.getValues().empresasSelecionadas.length === 0) {
      erros.push("Campo 'Empresas' não pode estar em branco quando a opção 'Gerar por Empresas' estiver selecionada.");
    }

    return erros;
  };

  return (
    <WindowComponent
      title="Recibo de Pagamento a Autônomo - RPA"
      useGlobalParameter={true}
      globalParameter={globalParameter}
      dependencies={[() => hasEmpresa(globalParameter), () => hasEmpresaMFP(globalParameter), () => hasGrupoEmpresaConfig(globalParameter)]}
      wikiId={3905}
    >
      <ReportForm
        {...props}
        handleSubmit={handleSubmit}
        formProps={formProps}
        localStorageKey={CLASS_KEY}
        routeProps={props}
        reportBasicValues={DEFAULT_VALUES}
        reportViewOptions={DEFAULT_OPTIONS}
        validateExtras={validateExtras}
      >
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true } }}
              id="referencia"
              label="Referência"
              placeholder="Informe a Referência"
              isPrependLabel={true}
              type={InputType.REFERENCE}
            />
          </Col>
        </Row>
        <Row>
          <Col className="col-4">
            <CustomInput
              type="switch"
              id="gerarPorEmpresas"
              name="gerarPorEmpresas"
              label="Gerar por Empresas"
              innerRef={formProps.register}
              onChange={onChangeGerarPorEmpresas}
            />
          </Col>
          <Col className="col-8">
            <EmpresaIS
              formProps={formProps}
              label="Empresa(s)"
              placeholder="Selecione no mínimo uma empresa"
              id="empresasSelecionadas"
              isPrependLabel={true}
              inputSearchProps={{ selectProps: { isMulti: true } }}
              tooltipProps={{ message: 'Empresas podem ser selecionadas.' }}
              disabled={!gerarPorEmpresas}
              dtoClassName={CLASS_NAME_EMPRESA}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <VinculoIS
              id="autonomos"
              formProps={formProps}
              label="Autônomos"
              placeholder="Selecione os Trabalhadores"
              isPrependLabel={true}
              inputSearchProps={{ selectProps: { isMulti: true } }}
              tooltipProps={{ message: 'Se nenhum autônomo for selecionado todos serão utilizados.' }}
              disabled={gerarPorEmpresas}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <TomadorServicoIS
              id="tomadores"
              formProps={formProps}
              label="Tomadores de Serviço"
              placeholder="Selecione os Tomadores"
              isPrependLabel={true}
              inputSearchProps={{ selectProps: { isMulti: true } }}
              tooltipProps={{ message: 'Se nenhum tomador for selecionado todos serão utilizados.' }}
              disabled={gerarPorEmpresas}
            />
          </Col>
        </Row>
      </ReportForm>
    </WindowComponent>
  );
};

export default RpaReportView;
